import * as React from 'react';

import { TCustomer } from './types';

export const CustomerCard = ({ data, ...props }: { data: TCustomer }) => {
  if (!data || !data.slug) return null;

  const { first_name, last_name, email, phone } = data;

  return (
    <div className="customer-card">
      <div className="fw-bold">{`${first_name} ${last_name}`}</div>
      <div className="">{phone}</div>
      <div className="text-muted text-sm">{email}</div>
    </div>
  );
};
